*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
  background-color: aquamarine;
}

.app.cold {
  background-image: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
.app.warm {
  background-image: url('./assets/warm-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}
main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  text-align: center;
  width: 33%;
  margin: 0 33% 75px;
}

.search-box .search-bar {
  /*
  display: block = makes input bar block
  width: 100% = stretches out the search bar width to max
  padding: 15px = makes the bar bigger in every direction
  appearance: none; = makes the white go away in the bar
  background: none; = makes white space go away again?
  border: none; = removes ugly black border
  outline: none; = does same thing?

  background-color: makes bar grey
  border-radius: makes bar round on bottom left and right
  margin-top: increases bar height to fit screen

  color: adds a color to the bottom of the search bar?
  font-size: increases font size of placeholder

  */
  display: block;
  width: 100%;
  padding: 15px;
  
  appearance: none;
  background: none;
  border: none;
  outline: none;
  

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  color: #313131;
  font-size: 20px;
  
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center
}

.weather-box .temp {
  /*
  position: relative 

  display: inline block -> you can add padding and margins to the block element
  margin: separates the block from everything else in every direction
  
  border-radius: circular border
  padding: expands the border

  the reason why inline block shrinks is because it wants to fit MORE stuff on the line
  if it just filled up the line, then there wouldnt be a point and we would just use block
  keyword INLINE, implying there will be MORE to the left and right 
  
  */
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;
  color: #FFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.50);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}